import React, {ChangeEvent, FC, useEffect, useState} from "react";

type PropsType={
    status:string
    updateStatus:(newStatus:string)=>void
}


const ProfileStatusWithHooks: FC <PropsType> = ({status,updateStatus}) => {

    let [editMode, setEditMode] = useState<boolean>(false);// state-  : 0  state,  1 .  .state
    let [localStateStatus, setStatus] = useState<string>(status);//   
    //     props ,        setState

    useEffect(() => {
        setStatus(status)
    }, [status])
    //  ,         ,      
    //  ,    render()  props.status       props.status  BLL
    //useEffect -       state,
    //            state,
    //    setState      state        state


    const activateEditMode = () => {
        setEditMode(true)
    }
    const deactivateEditMode = () => {
        setEditMode(false)
        updateStatus(localStateStatus);
    }
    const onStatusChange = (e:ChangeEvent<HTMLInputElement>) => {
        setStatus(e.currentTarget.value)
    }

    return (
        <>
            <div>
                {!editMode && <div>
                    <span onDoubleClick={activateEditMode}>{localStateStatus || '------'}</span>
                </div>}
            </div>
            <div>
                {editMode && <div>
                    <input onChange={onStatusChange} autoFocus={true} onBlur={deactivateEditMode}
                           value={localStateStatus}/>
                </div>}
            </div>
        </>
    )
}

export default ProfileStatusWithHooks;